
html,
body {
  margin: 0;
  padding: 0;
}
body {
  background-color: #2a2f4a;
  width: 100vw;
  height: 100%;
  overflow: hidden;
  position: fixed;
  top: 0;
  left: 0;
}
body:after {
  content: '';
  display: block;
  width: 1390px;
  height: 700px;
  background-color: #31ecdb;
  -webkit-transform: rotate(35deg);
          transform: rotate(35deg);
  -webkit-transform-origin: 0% 0%;
          transform-origin: 0% 0%;
  position: absolute;
  top: 270px;
  left: 0;
  z-index: -1;
}
#app {
  -webkit-transform: perspective(800px);
          transform: perspective(800px);
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  z-index: 2;
}
.acc-info {
  display: none;
  position: fixed;
  top: 0;
  right: 0;
  background-color: #000;
  color: #efefef;
  z-index: 99999;
  width: 200px;
  height: 100px;
}


